.container {
  width: 60px;
  height: 60px;
  border-radius: 100px;
  margin-right: 15px;
  border: rgba(193, 182, 173, 1) 1px solid;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.5s ease;
  cursor: pointer
}

.container:hover {
  background-color: #E1D4C9FF;
  transition: all 0.5s ease;
}

.container:hover .imgSvg path{
  stroke: #665F55FF;
  transition: all 0.5s ease;

}