.container {
  display: flex;
  justify-content: center;
  user-select: none;
}

.header {
  width: 1365px;
  height: 80px;
  top: 0;
  background: #E1D4C9;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: fixed;
  z-index: 2;
}

.logo {
  width: 100px;
  height: 60px;
  padding-top: 10px;
  display: flex;
}

.grad {
  position: fixed;
  top: 80px;
  width: 100%;
  background: linear-gradient(to bottom, rgba(225, 212, 201, 0.9), rgba(225, 212, 201, 0));
  z-index: 1;
  height: 10px;
}

.navBar {
  width: 600px;
  margin-left: 245px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  /*display: flex;*/
  /*justify-content: space-around;*/
  /*flex-direction: row;*/
  /*align-items: center;*/
  /*gap: 60px;*/
}

.navBarButton {
  position: relative;
  padding-bottom: 5px;
  cursor: pointer;
}

.navBarButton::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: 0;
  left: 50%;
  background-color: black;
  transition: width 0.3s ease-in-out, left 0.3s ease-in-out;
}

.navBarButton:hover::after {
  width: 100%;
  left: 0;
}

.coffeeMenu {
  /*padding-right: 40px;*/
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding-bottom: 5px;
  cursor: pointer;
  color: #403F3D;
}

.coffeeMenu::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: 0;
  left: 50%;
  background-color: black;
  transition: width 0.3s ease-in-out, left 0.3s ease-in-out;
}

.coffeeMenu:hover::after {
  width: 100%;
  left: 0;
}

.coffeeMenu > img {
  padding-left: 8px;
}

.buttonContainerTablet{
  display: none;
}

.cartShop {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 200px;
  z-index: 3;
  cursor: pointer;
}
.quantity {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  background-color: rgba(64, 63, 61, 0.8);
  color: white;
  border-radius: 50px;
  position: absolute;
  top: 50%;
  right: -15px;
  user-select: none;
}



@media (max-width: 1360px) {
  .header {
    width: 768px;
  }

  .coffeeMenu {
    display: none;
  }

  .burger {
    justify-content: flex-end;
    width: 0;
    margin-right: 90px;
  }
  .navBar {
    display: none;
  }
  .burgerButt {
    display: flex;
  }
  .buttonContainer {
    display: none;
  }
  .cartShop {
    padding-left: 200px;
  }

  .headerTabletContainer {
    display: flex;
  }

  .buttonContainerTablet {
    position: absolute;
    width: 44px;
    height: 44px;
    border: #665F55FF 1px solid;
    border-radius: 50px;
    /*background-color: black;*/
    top: 15px;
    right: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 3;
  }

  .buttonAll {
    height: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
  .buttonOne,
  .buttonTwo {
    width: 19px;
    height: 2px;
    border-radius: 50px;
    background-color: #665F55FF;
    transition: all 0.4s ease;
    position: absolute;
  }
  .buttonTwo {
    margin-top: 10px;
  }

  .buttonOne.active{
    transform: rotate(135deg);
    position: absolute;
    margin-top: 5px;
    transition: all 0.4s ease;
  }

  .buttonTwo.active{
    transform: rotate(-135deg);
    position: absolute;
    margin-top: 5px;
    transition: all 0.4s ease;
  }
}


@media (max-width: 768px) {
  .header {
    width: 355px;
  }

  .cartShop {
    padding-left: 30px;
  }

  .burger {
    justify-content: flex-end;
  }
  .navBar {
    display: none;
  }

  .burgerButt {
    display: flex;
  }

  .cartShop {
    margin-left: 0;
  }
}