.container {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-right: 20px;
    user-select: none;
}

.containerSliderBtn {
    display: flex;
    align-items: center;
    flex-direction: row;
}

.sliderContainer {
    position: relative;
    width: 1152px;
    height: 670px;
    overflow: hidden;
    display: flex;
    flex-direction: row;
}

article{
    position: absolute;
    width: 480px;
    height: 640px;
    opacity: 0;
    transition: all 0.7s linear;
    text-align: center;
    margin-left: 345px;
}

.textContainer {
    font-weight: 600;
    font-size: 24px;
}

.descriptionText {
    width: 480px;
    font-weight: 400;
    font-size: 16px;
    margin: 18px 0;
}


.slider {
    display: flex;
    flex-direction: column;

}

article.activeSlide {
    opacity: 1;
    transform: translateX(0);
}

article.lastSlide {
    transform: translateX(-100%);
}
article.nextSlide {
    transform: translateX(100%);
}

.dots {
    margin: 0 0 0 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-right: 10px;
    gap: 18px;
}

.dot {
    width: 40px;
    height: 4px;
    background-color: rgba(193, 182, 173, 1);
    border: none;
    cursor: pointer;
    transition: all 0.7s ease;
}

.dot.active {
    background-color: #665F55FF;
    transition: all 0.7s ease;
}


@media (max-width: 1360px) {
    .containerSliderBtn {
        width: 700px;
        margin-left: 78px;
    }

    .sliderContainer {
        width: 530px;
    }
    article{
        margin-left: 20px;
    }
}

@media (max-width: 768px) {

    .container {
        margin-right: 0;
    }

    .containerSliderBtn {
        width: 350px;
        margin-right: 90px;
    }

    .sliderContainer {
        width: 350px;
        height: 532px;
    }

    article{
        width: 350px;
        height: 640px;
    }

    .slider img {
        width: 350px;
        height: 350px;
    }

    .textContainer {
        width: 350px;
        height: 170px;
    }

    .descriptionText {
        width: 320px;
        margin: 18px 0 18px 10px;
    }

    .dots {
        margin:20px 0 0 30px;
        padding-right: 10px;
        gap: 18px;
    }

    .dot {
        width: 40px;
        height: 4px;
    }

}