.container {
  background-color: #665F55FF;
  width: 1360px;
  height: 400px;
  border-radius: 40px;
  margin: 30px 0;
  z-index: 1;
}

.containerAll {
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: space-around;
  padding-top: 80px;
}

.text {
  font-weight: 600;
  font-size: 60px;
  width: 530px;
  color: #E1D4C9FF;
}

.text i {
  color: rgba(176, 144, 122, 1);
}

.iconContainer {
  margin-top: 30px;
  display: flex;
}

.icons {
  margin-right: 15px;
  transition: all 0.3s ease;
}

.icons:hover {
  fill: #E1D4C9;
  transition: all 0.3s ease;
}

.icons:hover path {
  stroke: #403F3D;
  transition: all 0.3s ease;
}


.contactUs{
  padding: 0 100px 40px 0;
  color: #E1D4C9FF;
}

.contactUs div:nth-child(1) {
  font-weight: 600;
  font-size: 24px;
  margin-bottom: 30px;
}


.contactUs div{
  display: flex;
  align-items: center;
  margin: 20px 0;
}
.contactUs img {
  margin-right: 10px;
}

.svg{
  margin-right: 10px;
}

@media (max-width: 1360px) {

  .container {
    width: 688px;
    height: 736px;
  }

  .containerAll {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    margin-left: 50px;
  }

  .icons {
    margin-top: 50px;
  }
  .contactUs{
    margin-top: 100px;
    padding: 0;
    color: #E1D4C9FF;
  }
}

@media (max-width: 768px) {
  .container {
    width: 348px;
    height: 526px;
  }

  .containerAll {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    margin-left: 15px;
  }
  .text {
    font-size: 32px;
    width: 316px;
    color: #E1D4C9FF;
  }
  .icons {
    margin-top: 30px;
  }
  .contactUs{
    margin-top: 30px;
    padding: 0;
    color: #E1D4C9FF;
  }
}