.container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 80px 0 20px;
    user-select: none
}

.buttons {
    max-width: 370px;

}

.coffee,
.tea,
.dessert {
    height: 46px;
    border: rgba(193, 182, 173, 1) 1px solid;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    transition: border 0.5s ease, background-color 0.5s ease, color 0.5s ease;
    cursor: pointer;
}

.tea {
    margin: 0 20px;
}

.svg {
    border: rgba(193, 182, 173, 1) 1px solid;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 10px;
    background-color: rgba(193, 182, 173, 1);
}

.text {
    margin-right: 10px;
}

.container div{
    border-radius: 50px;
    display: flex;
    align-items: center;
    transition: border 0.5s ease, background-color 0.5s ease, color 0.1s ease;
}

.coffee:hover,
.tea:hover,
.dessert:hover {
    background-color: #665F55FF;
    color: rgba(193, 182, 173, 1);
    transition: border 0.5s ease, background-color 0.5s ease, color 0.1s ease;
}

.coffee.active,
.tea.active,
.dessert.active {
    background-color: #665F55FF;
    color: rgba(193, 182, 173, 1);
}

@media (max-width: 768px) {
    .tea {
        margin: 0 6px;
    }
}
