.container {
    width: 60px;
    height: 60px;
    border-radius: 100px;
    border: rgba(193, 182, 173, 1) 1px solid;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease;
    cursor: pointer;
}

.container:hover {
    background-color: #665F55FF;
    transition: all 0.5s ease;
}

.container:hover .svgArrow path {
    stroke: #E1D4C9FF;
    transition: all 0.5s ease;
}

@media (max-width: 768px) {
    .container {
        display: none;
    }


}