.container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 38px;
    z-index: 1;
    color: rgba(64, 63, 61, 1);
}

.infBlock {
    border: #C1B6ADFF 1px solid;
    border-radius: 40px;
    max-width: 310px;
    height: 500px;
    transition: all 0.5s ease;
}

.infBlock:hover {
    transition: all 0.5s ease;
}

.imgDiv {
    border: #C1B6ADFF 1px hidden;
    margin-right: 1px;
    width: 310px;
    height: 310px;
    border-radius: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.img {
    transition: transform .4s;
    user-select: none;
}

.img:hover  {
    transform: scale(0.93);
}

.textContainer {
    height: 150px;
    margin: 20px;
    color: #403F3DFF;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}

.titleDiv {
    font-weight: 600;
    font-size: 24px;
}

.priceDiv {
    font-weight: 600;
    font-size: 24px;
}

.modal {
    height: 100vh;
    width: 100vw;
    background-color: #0000007F;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: .5s;
    z-index: 102;
}

.modal.active {
    opacity: 1;
    pointer-events: all;
}

.modalContent {
    width: 800px;
    padding: 18px;
    border-radius: 45px;
    background-color: #E1D4C9FF;
    display: flex;
    flex-direction: row;
    transform: scale(0.5);
    transition: .4s all;
}

.modalContent.active {
    transform: scale(1);
}

.popupImg {
    height: 340px;
    min-width: 340px;
    border-radius: 40px;
    overflow: hidden;
}

.popupTextCont{
    margin-left: 18px;
}

.popupTitle{
    font-weight: 600;
    font-size: 24px;
    margin-bottom: 20px;
}

.priceContainer {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    font-weight: 600;
    font-size: 24px;
    margin-top: 10px;
}

.popupInf {
    display: flex;
    justify-content: space-between;
    font-weight: 600;
    font-size: 10px;
}

.popupSizeContainer,
.additivesContainer {
    margin-top: 20px;
}

.popupSize,
.popupAdditives {
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

.popupSizeM,
.additivesTwo {
    margin: 0 10px;
}

.popupSizeS,
.popupSizeM,
.popupSizeL,
.additivesOne,
.additivesTwo,
.additivesThree {
    border: #665F55FF 1px solid;
    height: 46px;
    border-radius: 50px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    transition: border 0.5s ease, background-color 0.5s ease, color 0.5s ease;
    user-select: none;
    cursor: pointer
}

.worS,
.worM,
.worL,
.numOne,
.numTwo,
.numThree {
    background-color: #C1B6ADFF;
    margin-left: 10px;
    border: #C1B6ADFF 1px solid;
    min-width: 30px;
    height: 30px;
    border-radius: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: border 0.5s ease, background-color 0.5s ease, color 0.5s ease;
}

.mlS,
.mlM,
.mlL,
.addOne,
.addTwo,
.addThree {
    margin: 0 10px;
}

.popupSizeS:hover .worS,
.popupSizeM:hover .worM,
.popupSizeL:hover .worL{
    color: #665F55FF;
    background-color: #E1D4C9FF;
}

.popupSizeS:hover,
.popupSizeM:hover,
.popupSizeL:hover {
    color: #E1D4C9FF;
    background-color: #665F55FF;
}

.popupSizeS.active .worS,
.popupSizeM.active .worM,
.popupSizeL.active .worL{
    color: #665F55FF;
    background-color: #E1D4C9FF;
}

.popupSizeS.active,
.popupSizeM.active,
.popupSizeL.active {
    color: #E1D4C9FF;
    background-color: #665F55FF;
}

.additivesOne:hover .numOne,
.additivesTwo:hover .numTwo,
.additivesThree:hover .numThree {
    color: #665F55FF;
    background-color: #E1D4C9FF;
}

.additivesOne:hover,
.additivesTwo:hover,
.additivesThree:hover {
    color: #E1D4C9FF;
    background-color: #665F55FF;
}

.popupInf div:nth-child(2) {
    margin-left: 10px;
}

.popupButton {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    user-select: none;
}

.popupButton div {
    cursor: pointer
}

.popupButton div {
    border: #665F55FF 1px solid;
    width: 210px;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50px;
    margin-top: 25px;
    transition: border 0.5s ease, background-color 0.5s ease, color 0.5s ease;
}

.popupButton div:hover {
    color: #E1D4C9FF;
    background-color: #665F55FF;
    transition: border 0.5s ease, background-color 0.5s ease, color 0.5s ease;
}

.modalContent {
    width: 800px;
    padding: 18px;
    border-radius: 45px;
    background-color: #E1D4C9FF;
    display: flex;
    flex-direction: row;
    transform: scale(0.5);
    transition: .4s all;
}

@media (max-width: 1360px) {
    .container {
        grid-template-columns: repeat(2, 1fr);
        gap: 38px;
    }
    .modalContent {
        width: 688px;
    }

    .infBlock {
        max-width: 330px;
    }

    .imgDiv {
        width: 323px;
        height: 323px;
    }

    .popupImg {
        height: 310px;
        min-width: 310px;
    }
    .popupTextCont {
        width: 346px;
    }
    .popupSize,
    .popupAdditives {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr ) ;
        gap: 5px;
        width: 250px;
    }
    .popupButton div{
        width: 158px;
    }
}

@media (max-width: 768px) {
    .container {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        gap: 38px;
        z-index: 1;
    }

    .infBlock {
        max-width: 338px;
    }

    .imgDiv {
        width: 338px;
        height: 323px;
    }

    .popupImg {
        display: none;
    }
    .modalContent {
        width: 342px;
        padding: 15px 0;
    }
    .popupTextCont {
        width: 310px;
    }
    .popupSize,
    .popupAdditives {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr ) ;
        width: 250px;
    }
    .popupButton div{
        width: 158px;
    }
}