.container {
  margin-top: 50px;
  font-weight: 600;
  font-size: 59px;
  color: #403F3DFF;
}

.textContainer {
  text-align: justify;
}

.container i{
  color: #B0907AFF;
}

.imagesContainer {
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  overflow: hidden;
}

.imagesContainer img {
  object-fit: cover;
  transition: transform .4s;
  user-select: none;
}

.imagesContainer img:hover  {
  transform: scale(0.9);
}

.imagesContainer div div{
  width: 650px;
  border: #E1D4C9FF 1px solid;
  border-radius: 40px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.imagesCol1 div:nth-child(1) {
  height: 590px;
}
.imagesCol1 div:nth-child(2) {
  margin-top: 60px;
  height: 430px;
}

.imagesCol2 {
  margin-left: 50px;
}

.imagesCol2 div:nth-child(1) {
  height: 430px;
}
.imagesCol2 div:nth-child(2) {
  margin-top: 60px;
  height: 590px;
}

@media (max-width: 1360px) {
  .imagesContainer {
    grid-template-columns: repeat(1, 1fr);
  }

  .imagesCol2 {
    margin-left: 0;
  }

  .imagesContainer div div{
    width: 680px;
    border-radius: 20px;
  }

  .imagesCol1 div:nth-child(2) {
    visibility: hidden;
    height: 0;
  }
  .imagesCol2 div:nth-child(1) {
    height: 0;
    visibility: hidden;
  }
  .imagesCol2 div:nth-child(2) {
    margin-top: 0;
    height: 590px;
  }
  .imagesContainer img:hover  {
    transform: scale(1);
  }
}

@media (max-width: 768px) {
  .container {
    font-size: 32px;
  }

  .imagesContainer {
    grid-template-columns: repeat(1, 1fr);
  }

  .imagesContainer div div{
    width: 348px;
    height: 1580px;
    border-radius: 20px;
  }

  .imagesCol1 div:nth-child(2) {
    visibility: hidden;
    height: 0;
  }
  .imagesCol2 div:nth-child(1) {
    height: 0;
    visibility: hidden;
  }
  .imagesCol2 div:nth-child(2) {
    margin-top: -30px;
    height: 590px;
  }
  .imagesContainer img:hover  {
    transform: scale(1);
  }
}