.shoppingCartContainer {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0, .5);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 1;
    pointer-events: all;
    transition: .5s;
    z-index: 101;
}

.shoppingCartContainer.active {
    opacity: 0;
    pointer-events: none;
}

.shoppingCart {
    width: 500px;
    background-color: #E1D4C9;
    color: #403F3D;
    position: fixed;
    overflow-y: auto;
    scrollbar-width: none;
    inset: 0 0 0 auto;
    display: grid;
    grid-template-rows: 70px 1fr 70px;
    z-index: 102;
    transition: all 0.3s ease;
}

.shoppingCart.active {
    transform: translateX(100%)
}

.shoppingCart h2 {
    padding: 20px;
    margin: 0;
    font-weight: 600;
    font-size: 36px;
}

.orderPrice {
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: fixed;
    width: 500px;
    height: 65px;
    background-color: #E1D4C9;
    top: 93%;
    margin-bottom: 10px;
}

.price {
    font-weight: 600;
    font-size: 30px;
    margin-right: 60px;
}

.btn {
    width: 220px;
    height: 50px;
    border: #665F55FF 1px solid;
    border-radius: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    transition: all 0.3s ease;
    user-select: none;
    cursor: pointer;
}

.btn:hover {
    background-color: #665F55FF;
    color: #E1D4C9FF;
    transition: all 0.5s ease;
}

.listCart {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    padding-left: 20px;
}

.shoppingContainer {
    width: 460px;
    height: 150px;
    border-radius: 30px;
    margin-bottom: 10px;
    background-color: rgba(102, 95, 85, 0.29);
    display: flex;
}

.img {
    width: 130px;
    height: 130px;
    border-radius: 20%;
    margin: 10px 0 0 10px;
}

.cartContainer {
    width: 290px;
    height: 125px;
    margin: 10px 0 0 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.textContainer {
    width: 290px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.svgX {
    cursor: pointer;
}

.size {
    font-size: 20px;
    font-weight: 600;
}

.priceContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
}

.quantityCart {
    width: 90px;
    height: 30px;
    border-radius: 50px;
    background-color: #E1D4C9;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 20px;
}



.title {
    font-weight: 600;
    font-size: 24px;
}

.button {
    width: 30px;
    height: 30px;
    border-radius: 50px;
    background-color: #E1D4C9;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease;
    cursor: pointer;
}

.button:hover {
    background-color: #665F55;
    color: #E1D4C9;
    transition: all 0.3s ease;
}


@media (max-width: 1360px) {

}

@media (max-width: 768px) {
    .shoppingCart {
        width: 100vw;
    }

    .shoppingCartContainer .btn div {
        width: 40vw;
        height: 50px;
    }

    .orderPrice {
        width: 102%;
        background-color: #E1D4C9;
        top: 93%;
    }

    .shoppingContainer {
        width: 99%;
    }

    .cartContainer {
        width: 75%;
    }

    .textContainer {
        width: 100%;
    }
}

@media (max-width: 666px) {
    .cartContainer {
        width: 70%;
    }
}

@media (max-width: 560px) {
    .cartContainer {
        width: 65%;
    }

    .orderPrice {
        margin-left: 10px;
    }
}