.headerTabletContainer {
    display: flex;
    z-index: 11;
}

.retractableHeader {
    /*display: none;*/
    width: 100vw;
    height: 100vh;
    background-color: #E1D4C9FF;
    color: rgba(64, 63, 61, 1);
    position: fixed;
    inset: 0 0 0 auto;
    transition: all 0.3s ease;
    z-index: 1;
}

.retractableText {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 150px;
    gap: 30px;
    font-weight: 600;
    font-size: 32px;
}

.retractableText div a {
    margin-top: 20px;
    text-decoration: none;
    color: rgba(64, 63, 61, 1);
}

.menuCup {
    margin-top: 20px;
}

.retractableHeader.active {
    transform: translateY(-120%)
}

.button {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}


@media (max-width: 1360px) {
    .headerTabletContainer {
        display: flex;
    }
}

